import React from 'react';
import './../scss/Filter.scss';
export default class Filter extends React.Component {
    constructor() {
        super();
        this.state = {
            lists:[
                {id:1,name:""},
                {id:2,name:""},
                {id:3,name:""},
                {id:4,name:""}
            ],
            addClass:1
        };
    }

    handleBackgroundColor(addClass){
        console.log(addClass);
        this.setState({
            addClass:addClass
        })
    }

    render() {
        return (
            <div className="filter">
                <div className="common">
                    <div className="bigSort">
                        <ul>
                        {
                            this.state.lists.map((value,index,array)=>{
                                console.log(value.id)
                                return <li data-key={'key'+value.id} className={this.state.addClass==value.id?"active":""}  onClick={()=>this.handleBackgroundColor(value.id)}>{value.name}</li>
                            })
                        }
                        </ul>
                    </div>
                    <div className="codeSort">
                        <div className="codeIndex">
                            <ul>
                                <li>A-G</li>
                                <li>H-N</li>
                                <li>O-T</li>
                                <li>U-Z</li>
                            </ul>
                        </div>
                        <div className="lineBox clearfix">
                             <span className="leftArrow"></span>
                             <div className="line"></div>
                             <span className="rightArrow"></span>
                        </div>
                    </div>
                    <div className="filterResult">
                        <ul className="clearfix">
                            <li>x</li>
                            <li>x</li>
                            <li>x</li>
                            <li>x</li>
                            <li>x</li>
                            <li></li>
                            <li>x</li>
                        </ul>
                    </div>
                </div>
            </div>
        );
    }
}